body{
    font-family: 'Poppins';
    background: #cd8764;
}

button{ 
    font-family: 'Poppins';
    width:18vmin;
    height:35px;
    border-radius:14px;
    border:none;
    cursor:pointer;
    background: #e4d4cc;
    color: #844f34;
    font-size: 14px;
    font-family: poppins;
}
button:hover{
    background:#cd8764;
    color:black
}
.splashEnable{
    background-color:#cd8764;
    width:100%;
    height:100vh;
    position:fixed;
    z-index:1000;
    font-family: poppins;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.splashEnable h1{
  font-size: 64px;
  color: white;
}
.splashDisable{
    display:none;
}
select{
    border:none;
    width:16vmin;
    cursor:pointer;
    border-radius: 10px;
    height: 30px;
    font-family: poppins;
    background: #e4d4cc;
    color: #844f34;
    font-size: 16px;
}
.mainDiv{
    display: flex;
    justify-content: space-evenly;
    width: 76%;
    height: 80vh;
    align-items: baseline;
    margin:auto;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    font-family: poppins;
    flex-direction: column;
}
input{
    width: 10vmin;
    height:30px;    
    border: none;
    border-radius: 6px;
    outline:none;
    font-family: poppins;
    background: #e4d4cc;
    color: #844f34;
}
.nav{
    width: 100vmax;
    height: 100px;
    background: #664428;
}

.menuDiv{
    background-color: black;
    animation: menuDeAnimation 1s;
    height: 0vh;
    position: absolute;
    width: 100vmax;
  }
  .menuDivActive{
    background-color: black;
    color: black;
    animation: menuAnimation 1s;
    height: 25vh;
    position: absolute;
    width: 100vmax;
  }
  
  @keyframes menuAnimation {
    from {
      height: 0vh;
    }
    to {
      height: 25vh;
    }
  }
  
  @keyframes menuDeAnimation {
    from {
      height: 25vh;
    }
    to {
      height: 0vh;
    }
  }
    
.load{
  width:60vh;
  height: 30px;
  animation:3s loading;
  background-color: black;
}
.loadMain{
  width:60vh;
  height: 29px;
  border: 1px solid black;
  background-color: white;
}
  @keyframes loading {
    from {width: 0px;
    }
    to {
      width: 60vh;
    }
  }
  
  h6{
      background: transparent;
      color: white;
      margin-top: 3%;
      font-size: 18px;
      cursor: pointer;
      position: absolute;
      font-weight: 400;
      margin-left: 5%;
  }
  h6:hover{
      color: #cd8764;
  }
  .cards{
      display: flex;
    width: 100%;
    height: 73%;
    box-shadow: none;
    margin: auto;
    align-items: center;
    justify-content: space-between;
  }
  .card{
      width: 29%;
      height: 99%;
      background: transparent;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      border-radius: 30px;
      box-sizing: border-box;
      box-shadow: inset 5px 5px 15px rgba(0,0,0,0.15),
      inset -5px -5px 15px rgba(255, 255, 255, 0.15),
      5px 5px 15px rgba(0,0,0,0.15),
      -5px -5px 15px rgba(255, 255, 255, 0.15);
  }
  .card button{
    align-items: center;
  }
  @media screen and (max-width: 960px){
      .card{
          font-size: 10px;
      }
  }
  @media screen and (max-width:530px) {
    .mainDiv{
      width: 98vh;
    }
  }
  @media screen and (max-width:700px) {
    .cards{
      flex-direction: column;
    }
  }
  path{
      
    stroke: rgb(83, 82, 82);
    stroke-width: 2;
  }
  .next{
    padding: 12px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .next:hover{
    background: #cd8764;
    
    box-shadow: inset 5px 5px 15px rgba(0,0,0,0.15),
    inset -5px -5px 15px rgba(255, 255, 255, 0.15),
    5px 5px 15px rgba(0,0,0,0.15),
    -5px -5px 15px rgba(255, 255, 255, 0.15);
  }
  .loader{
    
    width: 100vw;
    height: 100vh;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(3px);
  }